var conway = require('../../lib/conway-state-machine');

var singleton;

class GameOfLife {
    /**
     * 
     * @param {*} emerga 
     */
    constructor(emerga) {
        singleton = this;

        this._emerga = emerga;
        this.inited = false;
        this._globalCompositeOperation = 'luminosity';
        this._cellSize = 30;
        this._percentAlive = 8;
        this._ui = require('./ui');
        this._scaleFactor = 512;
        this.zcnt = 0;

        this.setupStateMachine();
    }
    get ui () { return this._ui; }
    set ui (v) { this._ui = v; }

    get emerga() {return this._emerga;}

    get globalCompositeOperation () {return this._globalCompositeOperation;}
    set globalCompositeOperation (v) {this._globalCompositeOperation=v;this.settingsUpdated();}

    get percentAlive () { return this._percentAlive; }
    set percentAlive (v) { this._percentAlive = v; }

    get cellSize () { return this._cellSize; }
    set cellSize (v) { 
        this._cellSize = v; 
        this.settingsUpdated();
    }

    get gridWidth () { return this.stateMachine.gridWidth; }
    set gridWidth (v) { this.stateMachine.gridWidth = v / this.stateMachine.cellSize; }

    get gridHeight () { return this.stateMachine.gridHeight; }
    set gridHeight (v) { this.stateMachine.gridHeight = v / this.stateMachine.cellSize; }

    get scaleFactor () { return this._scaleFactor; }
    set scaleFactor (v) { this._scaleFactor = v; }
    /**
     * 
     * @param {*} machine 
     * @param {*} els 
     */
    iterationEmitted(machine, els) {
        setTimeout(()=>{
            if(machine.rateOfChange === 0) {
                singleton.zcnt++;
                if(singleton.zcnt === 10) {
                    singleton.zcnt = 0;
                    return singleton.restart();
                }
            }
            singleton.ui.addToRender(els);
        },0);
    }
    /**
     * 
     */
    settingsUpdated() {
        singleton.restart();
    }
    /**
     * 
     */
    setupStateMachine() {
        this.stateMachine = new conway.ConwayStateMachine(120, 80, this._cellSize, this._percentAlive);
        this.stateMachine.onEmitIteration = this.iterationEmitted;
    }
    /**
     * 
     */
    restart() {
        if(!singleton.inited) return;
        if(singleton.stateMachine && !singleton.stateMachine.halted) {
            singleton.stateMachine.stop();
            singleton.setupStateMachine();
            singleton.inited = true;
            singleton.stateMachine.run(-1);
        }
    }
    /**
     * 
     */
    init() {
        this.ui.initUI(this);
    }
    /**
     * 
     */
    run() {
        restart();
        this.ui.animate(this);
    }
}
module.exports = GameOfLife;