/**
 * 
 * @param {*} app 
 */
const initControls = (app) => {
    g = app.emerga.guis['gui'];

    var folder = g.addFolder('Starting Conditions');
    folder.add(app, 'percentAlive', 1, 99).step(1).onChange(app.settingsUpdated());
    folder.open();

    const rulesObj = app.stateMachine.rules;
    var fr = folder.addFolder('Game of life Rules');
    var fonr = fr.addFolder('On State Rules');
    var foffr = fr.addFolder('Off State Rules');
    fonr.add(rulesObj, 'On1').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On2').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On3').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On4').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On5').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On6').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On7').onChange(app.settingsUpdated());
    fonr.add(rulesObj, 'On8').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off1').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off2').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off3').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off4').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off5').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off6').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off7').onChange(app.settingsUpdated());
    foffr.add(rulesObj, 'Off8').onChange(app.settingsUpdated());
    fr.open();

    folder = g.addFolder('Rendering');
    //folder.add(app, 'cellSize', 1, 100).step(1).onChange(app.settingsUpdated());
    folder.add(app, 'globalCompositeOperation', [ 
        'source-over', 
        'source-in', 
        'source-out',
        'source-stop',
        'destination-over',
        'destination-in',
        'destination-out',
        'destination-stop',
        'lighter',
        'copy',
        'xor',
        'multiply',
        'screen',
        'overlay',
        'darken',
        'lighten',
        'color-dodge',
        'color-burn',
        'hard-light',
        'difference',
        'exclusion',
        'hue',
        'saturation',
        'color',
        'luminosity'
    ]).onChange(app.settingsUpdated());
    folder.open();
    g.remember(app);
        
    folder = g.addFolder('Scaling');
    folder.add(app, 'scaleFactor', 1, 8192).onChange(app.settingsUpdated());
    folder.open();
};
/**
 * 
 * @param {*} app 
 */
const initUI = (app) => {
    app.emerga
        .initStats(0)
        .initGUI('gui', {
            load: JSON,
            preset: 'Game of Life'
        });
    initToolbar(app);
    initCanvas(app);
    initControls(app);
};
/**
 * 
 * @param {*} app 
 * @param {*} el 
 */
const renderElement = (app, el) => {
    if(!el || !el.updated) {
        return;
    }
    const ctx = app.emerga.context;
    var x = el.x * app.cellSize,
        y = el.y * app.cellSize,
        color = el.cellState ? 'rgba(255,255,255,1)' : 'rgba(0,0,0,1)'; 
    //ctx.globalCompositeOperation = app.globalCompositeOperation;
    ctx.fillStyle = color;
    ctx.fillRect(x, y,  app.cellSize,  app.cellSize);
    ctx.lineWidth = 2;
    ctx.strokeStyle = 'rgba(0,0,0, 1)';
    ctx.strokeRect(x, y,  app.cellSize,  app.cellSize);
};
/**
 * 
 */
var elementsToRender = [];
const addToRender = (el) => {
    if(Array.isArray(el)) {
        elementsToRender = el;
    }
   else{
    elementsToRender.push(el);
   } 
};
/**
 * 
 * @param {*} app 
 */
const animate = (app) => {
    clearCanvas(app);
    var _animate = () => {
        app.emerga.stats[0].begin();
        var el = elementsToRender.pop();
        while(el) {
            renderElement(app, el);
            el = elementsToRender.pop();
        }
        app.emerga.stats[0].end();
        requestAnimationFrame(_animate);
    };
    requestAnimationFrame(_animate);    
};

export { initUI, initCanvas, animate, addToRender }