const state = require('./state-machine');

var singleton;

class Cell extends state.EntityElement {
    constructor(machine, state, callback) {
        super(machine, state, callback);
    }
    get id() { return this.state.id; }
    get cellState() { return this.state.state; }
    set cellState(val) { this.state.state = val; }
    get updated() { return this.state.updated; }
    set updated(val) { this.state.updated = val; }
    get neighborCount() {
        const cellStateInt = (bx, by) => {
            const el = this.machine.elements.get(bx, by);
            return el.cellState ? 1 : 0;
        };
        const x = this.x, y = this.y;
        return cellStateInt( x - 1, y - 1) + 
            cellStateInt( x - 1, y) + 
            cellStateInt( x - 1, y + 1) + 
            cellStateInt( x, y - 1) + 
            cellStateInt( x, y + 1) + 
            cellStateInt( x + 1, y - 1) + 
            cellStateInt( x + 1, y) + 
            cellStateInt( x + 1, y + 1);
    }
    nextState() {
        const neighbors = this.neighborCount;
        return this.machine.rules.evaluate(this.cellState, neighbors);
    }
    static cellId(x, y) {
        return 'X' + x + 'Y' + y;
    }
    static advance(el) {
        var nextState = el.nextState();
        var updated = el.cellState !== nextState;
        el.state.next = nextState;
        el.updated = updated;
        return [el];
    }
}

/**
 * 
 */
class StateMachineWrappingGridCollection extends state.StateMachineCollection {
    constructor(x, y, machine) {
        super(machine);
        this._x = x;
        this._y = y;
        this.clear();
    }
    add(x, y, el) {
        this.elements[x][y] = el;
        return this;
    }
    get(x, y) {
        x = x < 0 ? this._x - 1 : x;
        y = y < 0 ? this._y - 1 : y;
        x = x > this._x - 1 ? 0 : x;
        y = y > this._y - 1 ? 0 : y;
        return this.elements[x][y];
    }
    clear() {
        this.elements = [];
        for(var i=0;i<this._x;i++)
            this.elements[i] = [];
    }
    toArray() {
        return [].concat.apply([], this.elements);
    }
}

class ConwayStateMachineRules {
    constructor(app) {
        this.app = app;
        this._On1 = false;
        this._On2 = true;
        this._On3 = true;
        this._On4 = false;
        this._On5 = false;
        this._On6 = false;
        this._On7 = false;
        this._On8 = false;
        this._On9 = false;
        this._Off1 = false;
        this._Off2 = false;
        this._Off3 = true;
        this._Off4 = false;
        this._Off5 = false;
        this._Off6 = false;
        this._Off7 = false;
        this._Off8 = false;
        this._Off9 = false;
    }
    get On1() { return this._On1; }
    get On2() { return this._On2; }
    get On3() { return this._On3; }
    get On4() { return this._On4; }
    get On5() { return this._On5; }
    get On6() { return this._On6; }
    get On7() { return this._On7; }
    get On8() { return this._On8; }
    set On1(v) { this._On1 = v; this.app.rulesUpdated(); }
    set On2(v) { this._On2 = v; this.app.rulesUpdated(); }
    set On3(v) { this._On3 = v; this.app.rulesUpdated(); }
    set On4(v) { this._On4 = v; this.app.rulesUpdated(); }
    set On5(v) { this._On5 = v; this.app.rulesUpdated(); }
    set On6(v) { this._On6 = v; this.app.rulesUpdated(); }
    set On7(v) { this._On7 = v; this.app.rulesUpdated(); }
    set On8(v) { this._On8 = v; this.app.rulesUpdated(); }

    get Off1() { return this._Off1; }
    get Off2() { return this._Off2; }
    get Off3() { return this._Off3; }
    get Off4() { return this._Off4; }
    get Off5() { return this._Off5; }
    get Off6() { return this._Off6; }
    get Off7() { return this._Off7; }
    get Off8() { return this._Off8; }
    set Off1(v) { this._Off1 = v; this.app.rulesUpdated(); }
    set Off2(v) { this._Off2 = v; this.app.rulesUpdated(); }
    set Off3(v) { this._Off3 = v; this.app.rulesUpdated(); }
    set Off4(v) { this._Off4 = v; this.app.rulesUpdated(); }
    set Off5(v) { this._Off5 = v; this.app.rulesUpdated(); }
    set Off6(v) { this._Off6 = v; this.app.rulesUpdated(); }
    set Off7(v) { this._Off7 = v; this.app.rulesUpdated(); }
    set Off8(v) { this._Off8 = v; this.app.rulesUpdated(); }

    evaluate(state, neighbors) {
        var startState = state ? '_On' : '_Off';
        startState += neighbors;
        return this[startState];
    }
}

class ConwayStateMachine extends state.StateMachine {
    /**
     * 
     * @param {*} emerga 
     */
    constructor(width, height, cellSize, percentAlive) {
        super();

        singleton = this;

        this._cellSize = cellSize;
        this._gridWidth = width;
        this._gridHeight = height;
        this._percentAlive = percentAlive;
        this._onEmitElement = null;
        this._onEmitIteration = null;

        this._rules = new ConwayStateMachineRules(this);

        this.gridNumbers = [];

        this._gridHeight = height;
        this.elements = new StateMachineWrappingGridCollection(width, height, this);

        var machine = this;
        this.builders.add(new state.StateBuilder(machine, null, () => {
            for(var x = 0; x < machine.gridWidth; x++) {
                for(var y = 0; y < machine.gridHeight; y++) {
                    const cellCnt = machine.gridWidth * machine.gridHeight;
                    var nel = new Cell(
                        machine, {
                            id: Cell.cellId(x, y), 
                            x, 
                            y, 
                            state: (Math.random() * cellCnt) < 
                                (cellCnt / machine.percentAlive),
                            updated: true
                        }, Cell.advance);
                    machine.elements.add(x, y, nel);
                }
            }
        }));
        this.postIteration = (machine, els) => {
            var gn = 0;
            machine.doEmitIteration(els);
            els.forEach((el) => {
                gn += el.cellState ? ((el.x * machine.gridWidth) + el.y) / machine.gridWidth : 0;
                el.cellState = el.state.next;
            });
            machine.gridNumber = gn;
        };
        machine.renderers.add(new state.StateRenderer(machine, null, (el) =>
            machine.doEmitElement(el)
        ));
    }

    get gridNumber () { 
        return this.gridNumbers.reduce((a,b) => a + b, 0) / this.gridNumbers.length;
    }
    set gridNumber (v) {
        this.gridNumbers.unshift(v);
        if(this.gridNumbers.length>9) {
            this.gridNumbers.pop();
        }
    }

    get rateOfChange () {
        var roc = [];
        function getChange(element, index, array) {
            if(index - 1 >= 0) {
                roc.push(element - array[index - 1]);
            }
        }
        this.gridNumbers.forEach(getChange);
        return roc.reduce((a,b) => a + b, 0) / roc.length;
    }
    get rules () { return this._rules; }

    get gridWidth () { return this._gridWidth; }
    set gridWidth (v) { this._gridWidth = v; }
    
    get gridHeight () { return this._gridHeight; }
    set gridHeight (v) { this._gridHeight = v; }

    get percentAlive () { return this._percentAlive; }
    set percentAlive (v) { this._percentAlive = v; }

    get cellSize () { return this._cellSize; }
    set cellSize (v) { this._cellSize = v; }

    get onEmitElement () { return this._onEmitElement; }
    set onEmitElement (v) { this._onEmitElement = v; }
    
    get onEmitIteration () { return this._onEmitIteration; }
    set onEmitIteration (v) { this._onEmitIteration = v; }

    rulesUpdated() {
        singleton.stop();
        setTimeout(() => {
            singleton.run(-1);
        });
    }
    doEmitElement(el) {
        if(this.onEmitElement)
            this.onEmitElement(this, el);
    }

    doEmitIteration(els) {
        if(this.onEmitIteration)
            this.onEmitIteration(this, els);
    }
}
module.exports = { ConwayStateMachine, StateMachineWrappingGridCollection, Cell };